.featured-hero{
  display:flex;
  height:100vh;
  align-items:center;
  justify-content:center;
  --tw-bg-opacity:1;
  background-color:rgba(0, 0, 0, var(--tw-bg-opacity))
}

.featured-hero__canvas{
  background-image:url(/react-developer-assessment/static/media/books.a5a57ec8.jpg);
  position:absolute;
  top:0px;
  right:0px;
  bottom:0px;
  left:0px;
  background-size:cover;
  background-attachment:fixed;
  background-position:center;
  background-repeat:repeat;
  opacity:0.4;
  -webkit-animation-name:glow;
  animation-name:glow;
  -webkit-animation-duration:3s;
  animation-duration:3s
}

.featured-hero__textbox{
  position:relative;
  padding-left:2.5rem;
  padding-right:2.5rem;
  text-align:center;
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity));
  -webkit-animation-name:fade;
  animation-name:fade;
  -webkit-animation-duration:2s;
  animation-duration:2s
}

.featured-hero__textbox .heading,.featured-hero__textbox .sunheading{
  -webkit-animation-name:letterTighten;
  animation-name:letterTighten;
  -webkit-animation-duration:2s;
  animation-duration:2s
}

.featured-hero__textbox .subheading{
  padding-top:1.5rem
}

