.featured-items__nav{
  margin-left:auto;
  margin-right:auto;
  margin-top:2.5rem;
  display:flex;
  border-right-width:0px;
  border-left-width:0px;
  border-style:solid;
  --tw-border-opacity:1;
  border-color:rgba(229, 231, 235, var(--tw-border-opacity));
  padding-top:1rem;
  padding-bottom:1rem
}

.featured-items__nav select{
  border-width:1px;
  margin-left:0.25rem;
  width:100%;
  cursor:pointer;
  border-radius:0.125rem;
  border-style:solid;
  --tw-border-opacity:1;
  border-color:rgba(229, 231, 235, var(--tw-border-opacity));
  padding:0.25rem;
  font-size:0.875rem;
  line-height:1.25rem
}

.featured-items__nav select:focus{
  outline:2px solid transparent;
  outline-offset:2px
}

